.dropdown{
    z-index: 3;
    position: absolute;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    background-color: #ffffff;
    max-height: 300px;
    overflow-x: hidden;
    overflow-y: scroll;
    border: solid 1px #cccccc;
    border-radius: 10px;
    margin-top: 35px;
}

